@import url(https://fonts.googleapis.com/css2?family=Urbanist&display=swap);
*{
    padding: 0;
    margin:0;
    font-family: 'Urbanist', sans-serif;
}

.container{
    flex-direction: column;
    width: 100%;
    height: 100vh;
    background-color: #22242B;
    display: flex;
    justify-content: center;
    align-items: center;
}

.card{
    margin-top: 20px;
    position: relative;
    width: 500px;
    background-color: #292D38;
    padding: 20px;
    border-radius: 10px;
}

.quote{
    color: white;
    font-weight:300;
    font-size: 22px;
    letter-spacing: .5px;
    text-align: justify;
    font-style: italic;
}

.character{
    margin-top: 10px;
    color: rgb(184, 184, 184);
    font-weight:200;
    font-size: 20px;
    text-align: right;
}

.anime{
    /* transform:translateX(-290px) translateY(30px) rotate(-45deg); */
    padding: 10px;
    border-radius: 10px;
    color: white;
    font-weight:200;
    font-size: 60px;
    text-align: right;
    background: #FF5F6D;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #FFC371, #FF5F6D); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.btn{
    position: relative;
    padding: 10px 20px;
    margin: 20px;
    border-radius: 5px;
    border: none;
    color: white;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 1px;
   
    background: #FF5F6D;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #FFC371, #FF5F6D); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    transition: all 2s;
    cursor: pointer;
}

.err{
    background-color: #FF5F6D;
    color: white;
    text-align: center;
    font-size: 20px;
    font-weight: 400;
    -webkit-animation: scale 1s ease-in-out;
            animation: scale 1s ease-in-out;
    letter-spacing: .5px;
}

@-webkit-keyframes scale {
    0%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50%{
        -webkit-transform: scale(1.05);
                transform: scale(1.05);
    }
    
}

@keyframes scale {
    0%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50%{
        -webkit-transform: scale(1.05);
                transform: scale(1.05);
    }
    
}

@media (max-Width:700px) {
    .anime{
        padding: 10px;
        border-radius: 10px;
        font-size: 20px;
    }
    .quote{
        font-size: 18px;
    }
    .card{
        width: 300px;
        margin-top: 10px;
    }
}


.animation-rotate {
  -webkit-animation: animation 2000ms linear infinite both;
  animation: animation 3000ms linear infinite both;
}

